
.mem__main-container{
  display:flex;
  flex-direction: row;
  flex-wrap:wrap;
  align-items:center;
  justify-content:space-around;
  padding-bottom: 5%;
  margin-top: 0.8em;
  margin-left: 1.6em;
  margin-right: 1.6em;
  margin-bottom: 0.6em;
}

.mem__inner-left-container{
  display:flex;
  flex-direction: row;
  align-items:flex-start;
  justify-content:space-around;
}

.mem__selectors-btns-btn {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  min-width: 100px;
  border-radius: 6%;
  height: 44px;
  font-size: 15px;
  outline: none;
  background-color: white;
  border: 2px solid var(--color5-grey2);
}


.mem__selectors-btns-btn-active {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  min-width: 100px;
  border-radius: 6%;
  border:none;
  height: 44px;
  font-size: 15px;
  outline: none;
  background-color: #263056;
  color:white;
}